import React, { useState, useEffect } from "react";
import { DetailHeading } from "../Component/DetailHeading";
import { DetailMain } from "../Component/DetailMain";
import { DetailTeacher } from "../Component/DetailTeacher";
import { DetailBenefit } from "../Component/DetailBenefit";
import Axios from "axios";
import { nodeApiLink } from "../config/NodeServerLink";
import '../scssFile/thongtin.css'

export const Detail = (props) => {
    const [course, setcourse] = useState({});
    

    useEffect(() => {
        const getData = async (id) => {
            let data = await Axios.get(`${nodeApiLink}/api/course/get/${id}`);
            setcourse(data.data);
        };
        getData(props.match.params.id);
        return () => {
            //
        };
    }, [props.match.params.id]);
    return (
        <>
            <div className="fullwitdh">
                <div className="container">
                    <DetailHeading course={course} />
                </div>
            </div>
            <div>
                <div className="container">
                    <DetailMain course={course} />
                    <DetailBenefit />
                    <DetailTeacher course={course} />
                </div>
            </div>
        </>
    );
};
export default Detail
