import React, { useEffect } from "react";
import $ from "jquery";
import { Skeleton } from "@material-ui/lab";

export const DetailMain = (props) => {
    const { course } = props;
    useEffect(() => {
        $(".learning-item").click(function () {
            if ($(this).find("span.plus:visible").length === 0) {
                $(this).find("span.plus").css("display", "inline-block");
                $(this).find("span.minus").css("display", "none");
                $(this).next().slideToggle();
            } else {
                $(this).find("span.plus").css("display", "none");
                $(this).find("span.minus").css("display", "inline-block");
                $(this).next().slideToggle();
            }
        });
        return () => {
            $(".learning-item").unbind();
        };
    }, [course]);

    const detail2 = course.detail || [];
    return (
        <div>
            <div className="row">
                <div className="col-12 col-md-8">
                    <div className="title">
                        <h1>Nội dung khoá học</h1>
                    </div>
                    <div className="left-main-content">
                        {detail2?.length ? (
                            detail2.map((title) => {
                                return (
                                    <div key={title.title} style={{ display: "flex", flexDirection: "column" }}>
                                        <div className="learning-item">
                                            <div className="learning-item-left">
                                                <span className="title-wrapper">
                                                    <span className="toogle">
                                                        <span className="plus">
                                                            <i className="fas fa-plus"></i>
                                                        </span>
                                                        <span className="minus" style={{ display: "none" }}>
                                                            <i className="fas fa-minus"></i>
                                                        </span>
                                                    </span>
                                                    <span className="text-left">{title.title}</span>
                                                </span>
                                            </div>
                                            <div className="learning-item-right">
                                                <span className="lectures">{title.chapter.length} Bài giảng</span>
                                                <span className="time-learning">{title.totalTime}</span>
                                            </div>
                                        </div>
                                        <div style={{ display: "none" }}>
                                            {title.chapter.map((chapter) => {
                                                return (
                                                    <div
                                                        key={chapter.name}
                                                        className="lecture-container multi-collapse"
                                                    >
                                                        <div className="left-content">
                                                            <span className="play-circle">
                                                                <i className="far fa-play-circle"></i>
                                                            </span>
                                                            <div className="title-top">
                                                                <span className="title">
                                                                    <span>{chapter.name}</span>
                                                                </span>
                                                            </div>
                                                        </div>
                                                        <div className="right-content-preview-time">
                                                            <span>{chapter.time}</span>
                                                        </div>
                                                    </div>
                                                );
                                            })}
                                        </div>
                                    </div>
                                );
                            })
                        ) : (
                            <>
                                <Skeleton variant="rect" height={49} style={{ marginTop: 3 }} animation="wave" />
                                <Skeleton variant="rect" height={49} style={{ marginTop: 3 }} animation="wave" />
                                <Skeleton variant="rect" height={49} style={{ marginTop: 3 }} animation="wave" />
                                <Skeleton variant="rect" height={49} style={{ marginTop: 3 }} animation="wave" />
                            </>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
};
